import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'mypipe'
})
export class MypipePipe implements PipeTransform {

  transform(value: any, args?: any, args2?:any): any {
    if(args){
    value = "Mr. "+value+" "+args;
    }
    else{
      value = "Mr. "+value;
    }
    if(args2){
      value =value+" "+args2;
      }
     
    console.log(args);
    return value;
  }

}
